.Panel {
  padding: 1em;
}

.Panel label {
  display: block;
  margin-bottom: 0.5em;
}

@media (min-width: 480px) {
  .App {
    display: flex;
    height: 100vh;
  }

  .Panel {
    min-width: 350px;
    padding: 1em;
    overflow-y: auto;
    box-sizing: border-box;
  }

  .Panel--float {
    position: fixed;
    top: 20px;
    bottom: 40px;
    left: 10px;
    border-radius: 20px;
    background-color: white;
    box-shadow: 
      -5px -5px 20px rgba(0,0,0,0.2), 
      10px 10px 15px rgba(0,0,0,0.4);
    z-index: 2;
  }

  .App > canvas {
    flex: 1;
  }
}